.homeBanner .imgWrapper {
  width: 100%;
  height: 450px;
}

.imgWrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.productItem {
  overflow: hidden;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.productItem .imgWrapper {
  width: 100%;
  height: 400px;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
}

.featuredProducts .productItem .imgWrapper img {
  object-fit: contain;
}

.productItem .content {
  padding: 10px;
}

.productItem .content i,
.productDetailContent p i {
  color: #f9c300;
}

.productItem .content h5 {
  font-weight: 400;
  font-size: 16px;
  padding: 5px 0 0px;
  margin-bottom: 10px;
  font-family: var(--heading);
  min-height: 45px;

  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.productItem .content .offerPrice {
  font-weight: 700;
  padding-bottom: 10px;
  margin-bottom: 0;
  font-family: var(--paragraph);
}

.productItem .content .originalPrice {
  font-weight: 500;
  font-size: 14px;
  color: var(--text-sub);
  margin-left: 5px;
}

.productItem .content a {
  color: #000;
}

.productItem .content a:hover {
  color: var(--prim-color);
}


.productItem .content .discount,
.productDetailContent .discount {
  background-color: red;
  padding: 5px 10px;
  border-radius: 0px 10px;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  font-family: var(--paragraph);
}

.smallCTA .imgWrapper {
  height: 150px;
  border-radius: 10px;
  overflow: hidden;
}

.smallCTA .imgWrapper img {
  object-fit: cover;
}

.featuredProducts2 .productItem {
  position: relative;
}

.featuredProducts2 .content {
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
  padding: 15px 0;
  background: linear-gradient(0deg,
      rgba(0, 0, 0, 1) 0%,
      rgba(255, 255, 255, 0) 100%);
  text-align: center;
  color: #fff;
  font-family: var(--fancy);
}

.featuredProducts2 .content h3::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 2px;
  background-color: red;
}

.weddingSection .heading {
  margin-bottom: 20px;
}

.weddingSection .productItem {
  box-shadow: none;
}

.weddingSection .productItem .frame {
  object-fit: fill;
  position: absolute;
  top: 0;
}

.weddingSection .content {
  margin-top: 20px;
}

.weddingSection .content h5 {
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  font-family: var(--paragraph);
}