.productImageWrapper {
  padding: 0 45px;
}

.productImageWrapper .image-gallery-content .image-gallery-slide .image-gallery-image {
  max-height: 100%;
}

.productImageWrapper .image-gallery-slide-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.productImageWrapper .image-gallery-slides {
  height: 600px;
}

.productImageWrapper .image-gallery-slide {
  height: 100%;
}

.productDetailContent .offerPrice {
  font-size: 28px;
  color: #000;
  font-weight: 600;
  font-family: Poppins;
  margin: 15px 0 2px;
}

.productDetailContent .offerPrice span {
  font-size: 16px;
  font-weight: 500;
  color: #8b8b8b;
  margin-left: 10px;
}

.productDetailContent .disclaimer {
  font-size: 14px;
  font-family: Roboto;
  font-weight: 300;
}

.productDetailContent .heading h1 {
  font-weight: 300;
  font-family: Roboto;
  margin-bottom: 0;
  padding: 10px 0;
}

.productDetailContent .features {
  list-style: cjk-ideographic;
  padding-left: 30px;
}

.productDetailContent .features li {
  font-family: Poppins;
  padding: 0px 0 5px;
  font-size: 14px;
}

.qtySelect {
  display: flex;
  border: 1px solid #ddd;
  width: max-content;
  /* padding: 10px; */
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
}

.qtySelect input {
  background-color: transparent;
  border: none;
  width: 50px;
  height: 35px;
  font-size: 18px;
  font-family: "Roboto";
  text-align: center;
  border-right: 1px solid #ddd;
  border-left: 1px solid #ddd;
}

.qtySelect i {
  font-size: 18px;
  padding: 10px;
}

.productDetailContent .formLabel {
  font-size: 18px;
  font-weight: 600;
  color: #000;
}

.productDetailContent .nav-item .nav-link {
  color: #000;
  font-size: 14px;
  border: none;
}

.productDetailContent .nav-item .nav-link.active {
  border-bottom: 2px solid var(--prim-color);
}

.productDetailContent .description {
  list-style: none;
  padding-left: 0;
  padding-top: 10px;
}

.productDetailContent .description li {
  font-size: 14px;
  padding-bottom: 5px;
}

.productDetailContent .nav-tabs {
  border: none;
  padding-top: 10px;
}