.searchListHeader {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    margin: 20px 0 30px;
    padding-bottom: 15px;
    font-family: var(--paragraph);
    border-bottom: 1px solid #ddd;
}

.filterWrapper {
    margin-top: 60px;
}

.filterWrapper ul {
    list-style: none;
    padding-left: 0;
}

.filterWrapper h4 {
    font-family: var(--heading);
    padding-bottom: 10px;
    margin-bottom: 0;
}

.filterWrapper .accordion-body ul li {
    padding-top: 10px;
    font-family: var(--paragraph);
    font-size: 14px;
}

/* ------------------------ */

.filterWrapper .accordion-button {
    padding: 15px 0;
    font-family: var(--paragraph);
    font-size: 14px;
    font-weight: 500;
}

.filterWrapper .accordion-button:not(.collapsed) {
    background-color: transparent;
    color: var(--text-color);
    box-shadow: none;
}

.filterWrapper .accordion-item {
    border: none;
    border-bottom: 1px solid #8f8f8f !important;
}

.filterWrapper .accordion-body {
    padding: 0;
}

/* ---------------------- */

.filterWrapper .range-slider {
    height: 3px;
}

.filterWrapper .range-slider .range-slider__thumb {
    width: 18px;
    height: 18px;
    background: var(--prim-color);
}

.filterWrapper .range-slider .range-slider__range {
    background-color: var(--prim-color);
}

.filterToggleButton {
    position: fixed;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    display: none;
}