.formItem {
    padding: 5px 10px;
    border: 1px solid #b3b3b3;
    border-radius: 5px;
    outline: none;
    font-size: 14px;
}

.formItem:focus {
    border: 1px solid var(--prim-color);
}

.formLabel {
    font-family: var(--paragraph);
    color: var(--text-sub);
}

select.formItem {
    border: 1px solid #000;
}

.formRow {
    margin-bottom: 15px;
}