.cartCanvas {
    position: fixed;
    right: 0;
    top: 56px;
    width: 150px;
    height: 100%;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.24) -3px 0px 8px;
    padding: 10px 0;
    overflow-y: auto;
}

.cartSubTotal {
    position: fixed;
    bottom: 0px;
    text-align: center;
    padding: 10px 0;
    border-top: 1px solid #ddd;
    width: -webkit-fill-available;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
}

.cartSubTotal p {
    margin-bottom: 0;
    font-size: 12px;
    font-family: var(--paragraph);
}

.cartSubTotal h5 {
    margin-bottom: 0;
    font-size: 18px;
    font-family: var(--heading);
}

.cartSubTotal a {
    background-color: var(--prim-color);
    color: #fff;
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    float: inline-end;
}

.cartListItem {
    padding: 0 10px 10px;
    margin-top: 10px;
    border-bottom: 1px solid #ddd;
}

.cartListItem .imgWrapper {
    height: 150px;
    border-radius: 8px;
    overflow: hidden;
}

.cartListItem .imgWrapper img {
    object-fit: fill;

}

.cartListItem .content h5 {
    padding-top: 10px;
    font-size: 16px;
    font-weight: 500;
    font-family: var(--heading);
    text-align: center;
}

.cartListItem .qtySelect input {
    font-size: 14px;
}

.cartListItem .qtySelect i {
    padding: 7px;
    font-size: 14px;
}