/* ------------------- */

.btn1 {
  position: relative;
  font-size: 16px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 5px 10px;
  display: inline-block;
  border-radius: 8px;
  transition: all 0.2s;
  border: none;
  font-family: inherit;
  font-weight: 500;
  color: black;
  background-color: white;
  border: 1px solid var(--prim-color);
  margin: 10px 0 0;
}

.btn1:hover {
  transform: translateY(-3px);
  border: 1px solid transparent;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background-color: var(--prim-color);
  color: #fff;
}

.btn1:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn1::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
}

.btn1::after {
  background-color: #fff;
}

.btn1:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.btn1.selected {
  background-color: var(--prim-color);
  color: #fff;
}

