.primBtn {
  background-color: var(--prim-color);
  color: #fff;
  border: none;
  padding: 10px 0px;
  border-radius: 8px;
  margin-top: 20px;
  font-family: "Roboto";
  font-weight: 500;
  transition: 0.3s ease-in-out;
  border: 1px solid transparent;
  display: block;
}

.primBtn.transparent {
  background-color: transparent;
  border: 1px solid var(--prim-color);
  color: var(--prim-color);
  font-weight: 800;
}

.primBtn:hover {
  background-color: #303030;
  border: 1px solid transparent;
  color: #fff;
  font-weight: 500;
}

.filterBtnBlack {
  background-color: #0e0e0e;
  color: #fff;
  border-radius: 50px;
  padding: 5px 10px;
  border: none;
  font-size: 22px;
  font-weight: 400;
  font-family: var(--heading);
  width: 260px;
}
