main {
  margin-top: 55px;
}

section {
  padding-bottom: 40px;
}

.transparentSearch {
  border: none;
  background-color: transparent;
  color: var(--prim-color);
  width: 45px;
  border-radius: 50%;
  transition: 0.3s ease-in-out;
}

.transparentSearch:hover {
  background-color: var(--prim-color);
  color: #fff;
}

a {
  text-decoration: none;
  color: var(--prim-color);
  font-weight: 500;
}