@media only screen and (max-width: 991px) {
  .homeBanner .imgWrapper {
    height: 420px;
  }

  .productItem .imgWrapper {
    height: 250px;
  }

  .productItem .content .discount {
    font-size: 12px;
  }

  .productItem .content .offerPrice {
    font-size: 18px;
  }

  .smallCTA .imgWrapper {
    height: 100px;
  }

  nav .nav-item {
    border-bottom: 1px solid #be18513d;
    padding: 3px 0;
  }

  footer {
    margin-top: 80px;
  }

  footer .footerItem ul {
    padding-left: 0;
  }

  footer .features {
    margin-top: -80px;
  }

  footer .features .imgWrapper {
    margin: 0;
    padding: 0;
    height: 80px;
  }

  footer .features .imgWrapper img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  footer .features .imgWrapper p {
    font-size: 14px;
    display: none;
  }

  .filterToggleButton {
    display: block;
  }

  .filterWrapper {
    position: fixed;
    top: -5px;
    left: -1000px;
    height: 100vh;
    overflow-y: scroll;
    width: 100%;
    padding: 15px;
    background-color: #fff;
    z-index: 9;
  }

  .loginFormWrapper {
    margin-top: -130px;
  }

  .productImageWrapper {
    padding: 0;
  }

  .productImageWrapper .image-gallery-slides {
    height: 500px;
  }

  .cartCanvas {
    width: 160px;
  }

  .cartSubTotal a {
    float: none;
  }
}