footer {
  background-color: #4f464f;
  padding: 50px 0 10px;
  margin-top: 150px;
}

footer .footerItem {
  color: #fff;
}

footer .footerItem ul a {
  color: #fff;
  text-decoration: none;
  font-family: var(--paragraph);
  font-size: 14px;
  font-weight: 200;
}

footer .footerItem ul {
  list-style: none;
}

footer .footerItem ul li {
  padding: 3px 0;
}

footer .footerItem ul h5 {
  font-family: var(--heading);
  text-transform: uppercase;
  font-weight: 300;
}

footer .footerItem .contact {
  padding-left: 0px;
  padding: 3px 0;
}

footer .footerItem .social a {
  margin-right: 20px;
  font-size: 16px;
}

footer .features {
  margin-top: -170px;
  margin-bottom: 20px;
}

footer .features .imgWrapper {
  margin: 30px;
  padding: 20px;
  background-color: #fff;
  border: 1px solid var(--prim-color);
  border-radius: 8px;
  text-align: center;
}

footer .features .imgWrapper img {
  width: 100px;
  height: 100px;
  margin: auto;
}

footer .features .imgWrapper p {
  margin: 15px 0 0 0;
  font-family: var(--paragraph);
  font-size: 12px;
}