.LoginBanner .imgWrapper {
    height: 300px;
    border-radius: 10px;
    overflow: hidden;
    padding: 0;
}

.loginFormWrapper {
    padding: 20px;
    /* border: 1px solid #ddd; */
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background-color: #fff;
    margin-top: -200px;
}

.loginFormWrapper .formLabel {
    display: block;
}

.loginFormWrapper .formItem {
    width: 100%;
    padding: 10px 10px;
}

.formFancyHeading {
    background: linear-gradient(195deg, #ec407a, #d81b60);
    color: #fff;
    text-align: center;
    padding: 20px 10px;
    border-radius: 10px;
    margin: -60px auto 20px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 3px 10px;
}